.header  {
  padding: 23px 0;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  @media (max-width: 460px) {
    padding: 15px 0;
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__right {
    @media (max-width: 960px) {
      display: none;
    }
  }
}

.block-logo {
  display: flex;
  align-items: center;
}
.header-logo {
  margin-right: 58px;
  @media (max-width: 768px) {
    margin-right: 35px;
  }
  @media (max-width: 460px) {
    margin-right: 20px;
    width: 25%;
    max-width: 25%;
  }
  &:first-child {
    @media (max-width: 768px) {
      width: 200px;
    }
    @media (max-width: 460px) {
      width: 150px;
    }
  }
  &:nth-child(2) {
    @media (max-width: 768px) {
      width: 70px;
    }
    @media (max-width: 460px) {
      width: 50px;
    }
  }
  img {
    max-width: 100%;
  }
}
