@import 'fonts.scss';
@import 'var.scss';
@import 'general.scss';
@import 'header.scss';
@import 'footer.scss';


html {
	font-size: 16px;
	@media (max-width: 768px) {
		font-size: 14px;
	}
	@media (max-width: 460px) {
		font-size: 12px;
	}
}
body {
	background-color: #fff;
	margin: 0;
	padding: 0;
	font-family: 'Montserrat', sans-serif;
	color: #144a74;
}


.main {
	padding: 140px 0;
	background-image: url(../images/main_bg.png), url(../images/main.jpg);
	background-repeat: no-repeat;
	background-position: bottom, center;
	background-size: auto, cover;
	display: flex;
	align-items: center;
	height: 1100px;
	box-sizing: border-box;
	@media (max-width: 1100px) {
		height: 900px;
	}
	@media (max-width: 768px) {
		height: 700px;
		background-image: url(../images/main_bg_768.png), url(../images/main_768.jpg);
		background-position: bottom, right;
	}
	@media (max-width: 460px) {
		height: 620px;
		background-image: url(../images/main_bg_460.png), url(../images/main_460.jpg);
		background-position: bottom, right;
	}
	&__wrap {
		@media (max-width: 768px) {
			width: 100%;
			display: block;
		}
	}
	&__title {
		font-family: "Museo", sans-serif;
		color: #ffffff;
		text-shadow: 0 11px 38px rgba(0, 110, 215, 0.8);
		font-size: 77px;
		font-weight: 700;
		text-transform: uppercase;
		max-width: 839px;
		margin-top: 40px;
		margin-bottom: 29px;
		@media (max-width: 1100px) {
			font-size: 60px;
		}
		@media (max-width: 768px) {
			font-size: 40px;
			margin-top: 20px;
			max-width: 100%;
		}
		@media (max-width: 460px) {
			font-size: 36px;
		}
		@media (max-width: 400px) {
			font-size: 28px;
		}
	}
	&__subtitle {
		color: #ffffff;
		// color: rgba(19, 57, 156, 1);
		text-shadow: 0px 5px 12px rgba(19, 57, 156, 1);
		// text-shadow: 0px 5px 12px rgba(255, 255, 255, 1);
		font-size: 1.375rem;
		line-height: 30px;
		@media (max-width: 768px) {
			line-height: normal;
		}
	}
	&__description {
		color: #ffffff;
		font-size: 1rem;
		font-weight: 400;
		display: block;
		max-width: 211px;
		margin-right: 30px;
		@media (max-width: 460px) {
			margin-bottom: 15px;
		}
	}
}

.main-column {
	display: flex;
	align-items: center;
	margin-right: 150px;
	margin-top: 150px;
	@media (max-width: 768px) {
		margin-top: 20px;
	}
	@media (max-width: 460px) {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
	}
	&__wrap {
		display: flex;
		@media (max-width: 660px) {
			display: block;
		}
	}
	@media (max-width: 768px) {
		margin-right: 40px;
		height: 160px;
	}
	@media (max-width: 660px) {
		height: auto;
	}
	@media (max-width: 580px) {
		width: 100%;
		height: auto;
		margin-bottom: 20px;
	}
}


.steps {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	margin: 100px 0 0px 0;
	@media (max-width: 768px) {
		margin: 50px 0;
	}
	@media (max-width: 460px) {
		margin: 10px 0;
	}
	&__item {
		width: 33%;
		padding: 20px 170px 20px 43px;
		box-sizing: border-box;
		position: relative;
		@media (max-width: 1200px) {
			padding-right: 50px;
		}
		@media (max-width: 768px) {
			width: 100%;
			margin-bottom: 30px;
		}
		@media (max-width: 460px) {
			padding-left: 0;
		}
		&:before {
			content: '';
			background-image: url(../images/sprite.png);
			display: block;
			position: absolute;
			right: 70px;
			top: 25px;
			@media (max-width: 960px) {
				right: 20px;
			}
			@media (max-width: 800px) {
				right: 0;
			}
		}
		&:nth-child(1):before {
		  background-position: 0px 0px;
		  width: 112px;
		  height: 85px;
		}
		&:nth-child(2):before {
			background-position: 0px -87px;
		  width: 93px;
		  height: 91px;
		}
		&:nth-child(3):before {
			background-position: -114px 0px;
		  width: 67px;
		  height: 102px;
		}
	}
	&__number {
		color: #144a74;
		font-family: "Museo", sans-serif;
		font-weight: 700;
		font-size: 58px;
		display: block;
		margin-bottom: 30px;
		@media (max-width: 1200px) {
			margin-bottom: 40px;
		}
		@media (max-width: 460px) {
			font-size: 36px;
			margin-bottom: 20px;
		}
	}
	&__title {
		font-family: "Museo", sans-serif;
		font-weight: 700;
		font-size: 1.75rem;
		margin-bottom: 21px;
		margin-top: 0;
	}
	&__description {
		font-size: 12px;
	}
}


.ways {
	background-image: url(../images/ways.jpg);
	background-repeat: no-repeat;
	background-position: left;
	height: 775px;
	@media (max-width: 910px) {
		height: auto;
		background-position: top;
	}
	&__wrap {
		display: flex;
		height: 100%;
		@media (max-width: 910px) {
			display: block;
		}
		@media (max-width: 380px) {
			padding: 0;
		}
	}
	&__left {
		padding: 60px 280px 176px 0;
		position: relative;
		@media (max-width: 1200px) {
			padding-right: 60px;
		}
		@media (max-width: 910px) {
			padding-top: 0;
		}
		@media (max-width: 560px) {
			padding-right: 10px;
		}
	}
	&__right {
		width: 479px;
		min-width: 479px;
		background-color: #fff;
		padding: 14px 40px;
		box-sizing: border-box;
		@media (max-width: 910px) {
			width: 100%;
			padding-top: 30px;
			min-width: 0;
		}
		@media (max-width: 380px) {
			padding: 30px 20px 14px 20px;
		}
		h3 {
			margin-top: 0;
		}
	}
	&__description {
		color: #ffffff;
		font-size: 1.25rem;
		padding: 0 74px 0 44px;
		line-height: 30px;
		@media (max-width: 560px) {
			padding-right: 0;
		}
		@media (max-width: 460px) {
			line-height: 25px;
		}
	}
	&__list {
		margin: 30px 0;
	}
	.hashtag-white {
		position: absolute;
		bottom: 80px;
		left: 44px;
	}
}

.ways-item {
	margin-bottom: 30px;
	padding: 10px 0;
	&__top {
		position: relative;
		padding-left: 46px;
		margin-bottom: 21px;
		@media (max-width: 910px) {
			margin-bottom: 30px;
		}
	}
	&__number {
		color: #28baff;
		font-family: "Museo", sans-serif;
		font-weight: 700;
		font-size: 1.875rem;
		position: absolute;
		top: 0px;
		left: 0;
	}
	&__title {
		font-size: 1.125rem;
		font-family: 'AvenirNextCyr', sans-serif;
	}
	.btn, .btn-gradient {
		width: 100%;
		@media (max-width: 910px) {
			width: 300px;
		}
		@media (max-width: 560px) {
			width: 220px;
			height: 40px;
		}
	}
}

.social {
	display: flex;
	justify-content: space-between;
	@media (max-width: 910px) {
		width: 370px;
	}
	@media (max-width: 560px) {
		width: 220px;
	}
	&__item {
		border-radius: 4px;
		background-color: #0021a0;
		width: 109px;
		height: 67px;
		display: flex;
		align-items: center;
		justify-content: center;
		text-decoration: none;
		transition: all 0.2s ease;
		@media (max-width: 560px) {
			width: 60px;
			height: 40px;
		}
		&:hover {
			background-color: lighten(#0021a0, 10%);
		}
		&:before {
			content: '';
			display: block;
			background-image: url(../images/sprite.png);
		}
		&.vk:before {
			background-position: -146px -125px;
		  width: 22px;
		  height: 13px;
		}
		&.fb:before {
			background-position: -114px -140px;
		  width: 11px;
		  height: 21px;
		}
		&.inst:before {
			background-position: -146px -104px;
			width: 18px;
			height: 19px;
		}
	}
}

.slider-wrap {
	margin-bottom: 50px;
}

.slider-for {
	margin-bottom: 30px;
	&__img {
		background-size: cover;
		height: 652px;
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		@media (max-width: 1020px) {
			height: 580px;
		}
		@media (max-width: 768px) {
			height: 380px;
		}
	}
}

.slider-nav {
	padding: 0 270px;
	position: relative;
	@media (max-width: 1400px) {
		padding: 0 160px;
	}
	@media (max-width: 1200px) {
		padding: 0 90px;
	}
	@media (max-width: 768px) {
		padding: 0 62px;
	}
	&__item {
		margin: 0 10px;
		overflow: hidden;
	}
	&__img {
		width: 199px;
		height: 133px;
		background-size: cover;
		@media (max-width: 1020px) {
			width: 120px;
			height: 80px;
		}
	}
	.arrow-prev {
		position: absolute;
		bottom: 0;
		left: 0;
	}
	.arrow-next {
		position: absolute;
		bottom: 0;
		right: 0;
	}
}


.regions {
	background-image: url(../images/regions.jpg);
	background-position: right;
	background-repeat: no-repeat;
	height: 758px;
	margin-bottom: 70px;
	@media (max-width: 1800px) {
		background-position: right -230px top;
	}
	@media (max-width: 1120px) {
		height: auto;
		padding-top: 750px;
		background-position: left top;
	}
	@media (max-width: 768px) {
		background: none;
		padding-top: 0px;
		margin-bottom: 20px;
	}
	&__wrap {
		height: 100%;
	}
	&__left {
		width: 510px;
		min-width: 480px;
		height: 100%;
		background-color: #fff;
		padding: 14px 15px 14px 0;
		box-sizing: border-box;
		@media (max-width: 1120px) {
			width: 100%;
			min-width: 0px;
		}
	}
	&__right {
		display: none;
		@media (max-width: 768px) {
			display: block;
		}
		img {
			max-width: 100%;
		}
	}
	.main-title {
		margin-top: auto;
		@media (max-width: 1120px) {
			margin-top: 20px;
		}
		@media (max-width: 768px) {
			margin-top: 10px;
		}
	}
	&__description {
		font-size: 1.125rem;
		margin-bottom: 30px;
		padding: 0 44px;
		@media (max-width: 768px) {
			margin-top: 50px;
		}
		@media (max-width: 460px) {
			padding-right: 0;
			line-height: 23px;
		}
	}
	.link {
		display: block;
		@media (max-width: 768px) {
			margin-top: 40px;
		}
	}
	ul {
		list-style: none;
		padding: 0;
	}
	li {
		margin-bottom: 15px;
		font-size: 1.125rem;
		margin-right: 60px;
		max-width: 361px;
		padding-left: 30px;
		position: relative;
		@media (max-width: 460px) {
			margin-bottom: 10px;
		}
		span {
			color: #1b97ea;
			font-size: 1.125rem;
			font-weight: 700;
			font-family: 'AvenirNextCyr', sans-serif;
			position: absolute;
			top: 0;
			left: 0;
		}
	}
}


.map {
	padding: 214px 0 0px 0;
	background-image: url(../images/main_bg-dark.png);
	background-repeat: no-repeat;
	background-position: top;
	@media (max-width: 768px) {
		background-image: url(../images/main_bg-dark_768.png);
		padding-top: 84px;
	}
	@media (max-width: 460px) {
		background-image: url(../images/main_bg-dark_460.png);
		padding-top: 77px;
	}
	&__content {
		position: relative;
		padding-top: 100px;
		padding-bottom: 240px;
		height: 725px;
		background-color: #32405b;
		background-image: url(../images/main_bg.png);
		background-repeat: no-repeat;
		background-position: bottom;
		@media (max-width: 1240px) {
			padding-bottom: 20px;
		}
		@media (max-width: 768px) {
			height: auto;
			padding-bottom: 80px;
			background-image: url(../images/main_bg_768.png);
			padding-top: 50px;
		}
		@media (max-width: 460px) {
			background-image: url(../images/main_bg_460.png);
		}
	}
	&__img {
		position: absolute;
		left: 0px;
		width: 62.5%;
		overflow: hidden;
		@media (max-width: 1420px) {
			width: 68%;
		}
		@media (max-width: 1240px) {
			width: 60%;
			height: 460px;
		}
		@media (max-width: 768px) {
			position: relative;
			width: 100%;
			margin-bottom: 40px;
			box-sizing: border-box;
			padding: 0 10px;
			img {
				width: 100%;
				height: 100%;
			}
		}
		@media (max-width: 460px) {
			height: 300px;
		}
	}
	&__wrap {
		display: flex;
		justify-content: flex-end;
		@media (max-width: 768px) {
			display: block;
		}
	}
	&__right {
		width: 480px;
		min-width: 480px;
		height: 100%;
		padding: 0 62px;
		box-sizing: border-box;
		@media (max-width: 1420px) {
			width: 400px;
			min-width: 400px;
			padding: 0 30px;
		}
		@media (max-width: 1040px) {
			width: 300px;
			min-width: 300px;
		}
		@media (max-width: 768px) {
			width: 100%;
		}
		@media (max-width: 640px) {
			padding: 0;
		}
	}
	&__title {
		color: #ffffff;
		font-size: 48px;
		font-weight: 700;
		text-transform: uppercase;
		font-family: "Museo", sans-serif;
		margin-top: 0;
		margin-bottom: 60px;
		@media (max-width: 1420px) {
			font-size: 40px;
			margin-bottom: 40px;
		}
		@media (max-width: 1240px) {
			font-size: 30px;
		}
		@media (max-width: 540px) {
			font-size: 24px;
			margin-bottom: 25px;
		}
	}
}


.numeral {
	&__list {
		@media (max-width: 768px) {
			display: flex;
			justify-content: space-between;
		}
		@media (max-width: 540px) {
			display: block;
		}
	}
	&__item {
		margin-bottom: 75px;
		@media (max-width: 1240px) {
			margin-bottom: 50px;
		}
		@media (max-width: 540px) {
			margin-bottom: 30px;
		}
	}
	&__title {
		position: relative;
		padding-left: 48px;
		color: #ffffff;
		font-weight: 700;
		font-family: "Museo", sans-serif;
		margin-top: 0;
		margin-bottom: 5px;
		display: block;
		font-size: 58px;
		@media (max-width: 1240px) {
			font-size: 36px;
		}
		@media (max-width: 640px) {
			font-size: 26px;
			margin-bottom: 0;
		}
		&:before {
	    content: '';
	    background-image: url(../images/sprite.png);
	    background-position: -114px -104px;
	    width: 30px;
	    height: 34px;
	    display: block;
	    position: absolute;
	    left: 0;
	    top: 20px;
			@media (max-width: 1240px) {
				top: 5px;
			}
	  }
	}
	&__subtitle {
		color: #ffffff;
		font-size: 1rem;
		padding-left: 48px;
		display: block;
	}
}


.gallery {
	padding-top: 60px;
}
.slider {
	margin-top: 90px;
	padding-bottom: 50px;
	@media (max-width: 460px) {
		margin-top: 40px;
	}
	&__img {
		width: 480px;
		height: 559px;
		background-size: contain;
		background-repeat: no-repeat;
		@media (max-width: 960px) {
			height: 400px;
		}
		@media (max-width: 460px) {
			height: 230px;
		}
	}
	&__arrow-wrap {
		position: relative;
		margin-top: 50px;
	}
	.arrow-prev {
		position: absolute;
		bottom: 0;
		left: 0;
	}
	.arrow-next {
		position: absolute;
		bottom: 0;
		right: 0;
	}
}

.purpose {
	background-image: url(../images/purpose_bg.png);
	background-position: left bottom;
	background-repeat: no-repeat;
	padding-bottom: 250px;
	@media (max-width: 768px) {
		background-image: url(../images/purpose_bg_768.png);
		padding-bottom: 180px;
	}
	@media (max-width: 460px) {
		background-image: url(../images/purpose_bg_460.png);
		padding-bottom: 80px;
	}
	&__wrapper {
		display: flex;
		@media (max-width: 1300px) {
			display: block;
		}
	}
	&__title {
		color: #144a74;
		font-size: 48px;
		font-weight: 700;
		font-family: "Museo";
		text-transform: none;
		padding-left: 80px;
		position: relative;
		@media (max-width: 768px) {
	    font-size: 36px;
	  }
	  @media (max-width: 460px) {
	    font-size: 26px;
			padding-left: 45px;
	  }
	  @media (max-width: 340px) {
	    font-size: 20px;
	  }
		&.intent:before {
			content: '';
			width: 57px;
			height: 57px;
			background-image: url(../images/intent.png);
			display: block;
			position: absolute;
			left: 0;
			top: 5px;
			@media (max-width: 460px) {
				width: 30px;
				height: 30px;
				background-size: cover;
				top: 0;
			}
		}
		&.knowledge {
			padding-left: 100px;
			@media (max-width: 460px) {
				padding-left: 45px;
			}
		}
		&.knowledge:before {
			content: '';
			width: 67px;
			height: 55px;
			background-image: url(../images/knowledge.png);
			display: block;
			position: absolute;
			left: 0;
			top: 3px;
			@media (max-width: 460px) {
				width: 36px;
				height: 30px;
				background-size: cover;
			}
		}
	}
	&__description {
		font-size: 1.125rem;
		font-family: 'AvenirNextCyr', sans-serif;
	}
	&__column {
		&:first-child {
			width: 365px;
			margin-right: 80px;
			@media (max-width: 1300px) {
				width: auto;
			}
		}
	}
	&__list {
		list-style: none;
		padding: 0;
		height: 200px;
		display: flex;
		flex-direction: column;
		flex-wrap: wrap;
		@media (max-width: 930px) {
			height: auto;
		}
	}
	&__item {
		margin-bottom: 15px;
		font-size: 1.125rem;
		margin-right: 60px;
		max-width: 361px;
		font-family: 'AvenirNextCyr', sans-serif;
		padding-left: 40px;
		position: relative;
		@media (max-width: 1340px) {
			margin-right: 20px;
		}
		@media (max-width: 930px) {
			max-width: none;
		}
		@media (max-width: 460px) {
			padding-left: 25px;
		}
		span {
			color: #1b97ea;
			font-size: 1.125rem;
			font-weight: 700;
			font-family: 'AvenirNextCyr', sans-serif;
			position: absolute;
			top: 0;
			left: 0;
		}
	}
}
