h3 {
  color: #164b75;
  font-family: "Museo", sans-serif;
  font-weight: 700;
  font-size: 2.25rem;
  text-transform: uppercase;
}

.container {
  max-width: 1920px;
  // min-width: 100%;
  margin: 0 auto;
}

.text-container {
  max-width: 1460px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
  box-sizing: border-box;
  @media (max-width: 940px) {
    width: auto;
  }
}

.btn {
  width: 240px;
  height: 54px;
  border-radius: 4px;
  background-color: #0021a0;
  color: #ffffff;
  font-size: 1rem;
  font-weight: 700;
  font-family: 'Museo', sans-serif;
  border: none;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  &:hover {
    background-color: lighten(#0021a0, 10%);
  }
  @media (max-width: 460px) {
    width: 180px;
    height: 38px;
  }
}

.btn-gradient {
  width: 376px;
  height: 67px;
  border-radius: 4px;
  background-color: #0021a0;
  background-image: linear-gradient(-20deg, #0021a0 0%, #22b8ff 100%);
  color: #ffffff;
  font-weight: 700;
  font-family: 'Museo', sans-serif;
  font-size: 1.125rem;
  border: none;
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.4s ease;
  &:hover {
    background-image: linear-gradient(-10deg, #0021a0 0%, #22b8ff 100%);
  }
}

.btn-play {
  border-radius: 4px;
  background-color: #0021a0;
  background-image: linear-gradient(-20deg, #0021a0 0%, #22b8ff 100%);
  width: 57px;
  height: 54px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s ease;
  @media (max-width: 580px) {
    width: 43px;
    height: 40px;
  }
  @media (max-width: 460px) {
    width: 36px;
    height: 33px;
  }
  &:before {
    content: '';
    display: block;
    background-image: url(../images/sprite.png);
    background-position: -166px -104px;
    width: 15px;
    height: 17px;
  }
  &:hover {
    background-image: linear-gradient(-10deg, #0021a0 0%, #22b8ff 100%);
  }
}

.main-title {
  color: #144a74;
  font-weight: 700;
  font-family: 'Museo', sans-serif;
  font-size: 48px;
  text-transform: uppercase;
  position: relative;
  padding-left: 41px;
  @media (max-width: 768px) {
    font-size: 36px;
  }
  @media (max-width: 460px) {
    font-size: 26px;
  }
  @media (max-width: 340px) {
    font-size: 20px;
  }
  &:before {
    content: '';
    background-image: url(../images/sprite.png);
    background-position: -114px -104px;
    width: 30px;
    height: 34px;
    display: block;
    position: absolute;
    left: 0;
    top: 11px;
    @media (max-width: 460px) {
      top: 0px;
    }
  }
  &.white {
    color: #fff;
  }
}

a {
  color: #269dda;
}

.link {
  color: #269dda;
  font-size: 1.25rem;
  font-weight: 400;
  &:hover {
    text-decoration: none;
  }
}

.hashtag {
  color: #0622a7;
  font-weight: 700;
  font-family: 'Museo', sans-serif;
  font-size: 1.25rem;
  text-transform: uppercase;
  @media (max-width: 460px) {
    font-size: 1rem;
  }
}
.hashtag-white {
  color: #ffffff;
  font-size: 72px;
  font-family: 'Museo';
  font-weight: 700;
  @media (max-width: 1200px) {
    font-size: 40px;
  }
  @media (max-width: 460px) {
    font-size: 20px;
  }
}

.section {
  padding: 60px 10px;
  @media (max-width: 460px) {
    padding: 30px 10px;
  }
}


.arrow-prev {
  width: 72px;
  height: 14px;
  background-image: url(../images/sprite.svg#arrow-prev);
  cursor: pointer;
  @media (max-width: 768px) {
    width: 48px;
    height: 10px;
    background-size: cover;
  }
}
.arrow-next {
  width: 72px;
  height: 14px;
  background-image: url(../images/sprite.svg#arrow-next);
  background-position: center;
  cursor: pointer;
  @media (max-width: 768px) {
    width: 48px;
    height: 10px;
    background-size: cover;
  }
}
