.footer {
  background-color: #32405b;
  padding: 26px 0;
  @media (max-width: 560px) {
    padding: 10px 0;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0 40px 0;
    border-bottom: 1px solid #fff;
    @media (max-width: 560px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0;
    @media (max-width: 768px) {
      display: block;
    }
  }
}

.footer-logo {
  margin-right: 58px;
  @media (max-width: 960px) {
    margin-right: 35px;
  }
  @media (max-width: 560px) {
    &:nth-child(4) {
      margin-right: 0;
    }
  }
  @media (max-width: 460px) {
    margin-right: 20px;
    width: 25%;
    max-width: 25%;
    box-sizing: border-box;
    overflow: hidden;
  }
  &:first-child {
    @media (max-width: 960px) {
      width: 200px;
    }
    @media (max-width: 460px) {
      width: 150px;
    }
  }
  &:nth-child(2) {
    @media (max-width: 960px) {
      width: 70px;
    }
    @media (max-width: 460px) {
      width: 50px;
    }
  }
  img {
    max-width: 100%;
    @media (max-width: 360px) {
      width: 100%;
    }
  }
}
.footer-contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  @media (max-width: 560px) {
    align-items: center;
    margin-top: 30px;
  }
}
.footer-phone {
  color: #ffffff;
  font-family: "Museo", sans-serif;
  font-size: 1.3125rem;
  text-transform: uppercase;
  font-weight: 700;
  display: block;
  margin-bottom: 25px;
  text-decoration: none;
  @media (min-width: 560px) and (max-width: 1200px) {
    font-size: 16px;
    white-space: nowrap;
  }
}

.footer-social {
  display: flex;
  align-items: center;
  &__item {
    display: block;
    margin-right: 40px;
    &:last-child {
      margin-right: 0;
    }
    &.vk {
      background-image: url(../images/sprite.png);
      background-position: -146px -125px;
      width: 22px;
      height: 13px;
    }
    &.fb {
      background-image: url(../images/sprite.png);
      background-position: -114px -140px;
      width: 11px;
      height: 21px;
    }
    &.inst {
      background-image: url(../images/sprite.png);
      background-position: -146px -104px;
      width: 18px;
      height: 19px;
    }
  }
}

.copyright {
  color: #ffffff;
  font-family: 'Verdana', sans-serif;
  font-size: 0.8125rem;
  @media (max-width: 768px) {
    margin-bottom: 20px;
  }
}

.dev {
  color: #ffffff;
  font-family: 'Verdana', sans-serif;
  font-size: 0.8125rem;
  display: flex;
  align-items: center;
  @media (max-width: 460px) {
    display: block;
    span {
      display: block;
      margin-bottom: 0px;
    }
  }
  &__logo {
    margin-left: 24px;
    display: block;
    @media (max-width: 460px) {
      margin-left: 0;
      margin-right: 20px;
    }
  }
  &__logo-wrap {
    align-items: center;
    display: flex;
    @media (max-width: 460px) {
      margin-top: 10px;
    }
  }
}
